import React from "react";
import { Card } from "react-bootstrap";
import Johanna from "../images/Johanna.png"
import Estefania from "../images/Estefania.png"
import Richard from "../images/Richard.png"
import Manoj from "../images/Manoj.png"
import Natasha from "../images/Natasha.png"


export default class MemberComp extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    let contentObj = {
      name: "",
      position: "",
      country: "",
      picture: "",
    };

    if (this.props.member === "Johanna") {
      contentObj.name = this.props.member;
      contentObj.position = "Product Manager";
      contentObj.country = "Germany";
      contentObj.picture = Johanna;
    }
    if (this.props.member === "Richard") {
      contentObj.name = this.props.member;
      contentObj.position = "Interaction Designer";
      contentObj.country = "Nigeria";
      contentObj.picture = Richard;
    }
    if (this.props.member === "Estefania") {
      contentObj.name = this.props.member;
      contentObj.position = "AI Engineer";
      contentObj.country = "Peru";
      contentObj.picture = Estefania;
    }
    if (this.props.member === "Manoj Keerthy") {
      contentObj.name = this.props.member;
      contentObj.position = "Software Engineer";
      contentObj.country = "India";
      contentObj.picture = Manoj;
    }
    if (this.props.member === "Natasha") {
      contentObj.name = this.props.member;
      contentObj.position = "Software Engineer";
      contentObj.country = "Brazil";
      contentObj.picture = Natasha;
    }
    return (
      <div className="member-card">
      <Card className="card-class" style={{width: '18rem'}}>
        <Card.Body>
          <img src={contentObj.picture} style={{width: 155}} />
        </Card.Body>
        <Card.Body>  
          <Card.Title>{contentObj.name}</Card.Title>
          <Card.Subtitle><h7>{contentObj.position}</h7></Card.Subtitle>
          <Card.Text><h7>{contentObj.country}</h7></Card.Text>
        </Card.Body>
      </Card>
      </div>
    );
  }
}
